.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

input {
  padding: 10px;
  margin: 10px;
  font-size: 16px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  background: #61dafb;
  margin: 5px 0;
  padding: 10px;
  border-radius: 5px;
  color: #282c34;
}

.red{
  background-color: red;
  color: #fff; /* Set the text color to white to contrast with the dark background */
}

.large{
  
  color: yellow;
}